import classes from "./Button.module.scss";
import classNames from "classnames";
import { ButtonComponentProps } from "./Button.interface";

export const ButtonComponent = ({
  ui,
  size = "xl",
  className,
  ...restButtonProps
}: ButtonComponentProps) => {
  return (
    <button
      disableRipple
      className={classNames(
        className,
        classes.button,
        classes[size],
        ui && classes[ui]
      )}
      {...restButtonProps}
    />
  );
};
